            // Ïã§ÌñâÏù¥ ÎÅùÎÇú ÌõÑÏóêÎäî Îã§Ïãú throttleCheckÎ•º falseÎ°ú ÎßåÎì§Ïñ¥ Ï£ºÏñ¥, ÏÑ§Ï†ïÌïú Ï£ºÍ∏∞ÎßàÎã§ Ïù¥Î≤§Ìä∏Í∞Ä Ìïú Î≤àÏî©Îßå Ìò∏Ï∂úÎêòÎèÑÎ°ù ÌïòÏòÄÏäµÎãàÎã§.
